* {
	padding: 0;
	margin: 0;
	border: 0;
	box-sizing: border-box;
}

*,
*::after,
*::before {
	-moz-box-sizing: border-box;
	-webkit-box-sizing: border-box;
	box-sizing: border-box;
}

:active,
:focus {
	outline: none;
}

a:active,
a:focus {
	outline: none;
}

aside,
footer,
header,
nav {
	display: block;
}

body,
html {
	color: #333;
	margin: 0;
	padding: 0;
	border: 0;
	outline: 0;
	width: 100%;
	height: 100%;
	font-size: 100%;
	line-height: 1;
	background-size: 100%;
	font-size: 14px;
}

button,
input,
textarea {
	font-family: inherit;
}

input::-ms-clear {
	display: none;
}

input {
	outline: 0;
}

button {
	cursor: pointer;
}

button::-moz-focus-inner {
	padding: 0;
	border: 0;
}

a,
a:visited {
	text-decoration: none;
}

a:hover {
	text-decoration: none;
}

hr {
	height: 1px;
	width: 100%;
	background: #dddddd;
}

ul,
li {
	list-style: none;
}

label {
	width: fit-content;
}
